import {connect} from 'react-redux'
import {logout} from "../../redux/actions/login_action"
import React from 'react'
import { Divider, Card, Image, Input, Button, Space, Popconfirm } from 'antd'
import axios from "axios"
import { useNavigate } from 'react-router-dom'
import useInterval from '../../utils/timer'

import './index.scss'

const sampleDescription = "This is a sunny day. There is a girl wearing a white T-shirt. She is playing basketball. Meanwhile, she is gazing at a guy who is drinking. The guy perhaps is her boyfriend."
const sampleAction = "play;drink"

const { TextArea } = Input

const gridStyle = {
    width: '33%',
    textAlign: 'center',
}

const init_timer = 3000
const gap_timer = 1000

const initDescriptionList = [
    {description: '', action: ''},
    {description: '', action: ''},
]

function AnnotationUI (props) {
    const navigate = useNavigate()

    const [loadings, setLoadings] = React.useState(false)
    const [instance_id, setInstanceId] = React.useState(-1)
    const [imgList, setImgList] = React.useState([
        {'image': 'error'},
        {'image': 'error'},
        {'image': 'error'}
    ])
    const [descriptionList, setDescriptionList] = React.useState(initDescriptionList)
    const [img_id, changeImgId] = React.useState(2)
    const [timer, setTimer] = React.useState(init_timer)

    useInterval(() => {
        if (img_id >= 2) {
            if (imgList[0].image !== 'error') {
                changeImgId(0)
                setTimer(gap_timer)
            } else if (imgList[1].image !== 'error') {
                changeImgId(1)
                setTimer(gap_timer)
            }
        } else {
            if (img_id === 1) {
                setTimer(init_timer)
            }
            changeImgId(img_id + 1)
        }
    }, timer)
    

    React.useEffect(() => {
        axios.get(
            '/backend/annotation/add',
        ).then(
            function(response) {
                console.log(response.data)
                if (response.data.result === false) {
                    alert("")
                } else {
                    setInstanceId(response.data.instance_id)
                    setImgList(response.data.image)
                }
                
            },
            function(error) {
                console.log('', error)
            }
        )
    },[]) //

    const toHome = () => {
        navigate('/home', {
            replace: false,
            state: {},
        })
    }

    const addNewDescription = () => {
        setDescriptionList([...descriptionList, ...[{description: '', action: ''}]])
    }

    const toSubmit = () => {
        setLoadings(true)
        axios.post(
            '/backend/annotation/add',
            {
                user: {
                    user_id: props.state.user_id,
                    password: props.state.password,
                },
                instance_id: instance_id,
                description: descriptionList,
            }
        ).then(
            function(response) {
                // console.log('', response)
                if (response.data.result === false) {
                    alert(response.data.information)
                    setLoadings(false)
                } else {
                    alert('')
                    setLoadings(false)
                    window.location.reload()
                }
            },
            function(error) {
                setLoadings(false)
            }
        )
    }

    const changeDescription = (index, e) => {
        setDescriptionList(() => {
            descriptionList[index].description = e.target.value
            return descriptionList
        })
    }

    const changeAction = (index, e) => {
        setDescriptionList(() => {
            descriptionList[index].action = e.target.value
            return descriptionList
        })
    }

    const toHelp = () => {
        alert("    \n    jump;play;hold;stand")
    }

    const addHard = () => {
        setLoadings(true)
        axios.post(
            '/backend/annotation/ishard',
            {
                user: {
                    user_id: props.state.user_id,
                    password: props.state.password,
                },
                instance_id: instance_id,
            }
        ).then(
            function(response) {
                // console.log('', response)
                if (response.data.result === false) {
                    alert(response.data.information)
                    setLoadings(false)
                } else {
                    setLoadings(false)
                    window.location.reload()
                }
            },
            function(error) {
                alert('')
                setLoadings(false)
            }
        )
    }

    return (
        <div className='annotation'>
            <div>
                <h1>Grounding</h1>
                <h3></h3>
                <p></p>
                <p>stand,walkwear,have</p>
                <h4>;jump;play</h4>
                <Divider />
            </div>

            <div>
                <Card title="">
                    <Image
                        width={768}
                        height={480}
                        src={imgList[img_id].image}
                        fallback="data:image/png;base64,"
                    />
                </Card>
            </div>
            <Divider orientation="left" plain></Divider>
            <div>
                <Space size="large" direction='vertical'>
                    <Space size="large">
                        <Button type="primary" onClick={toSubmit} loading={loadings}></Button>
                        <Button type="primary" danger onClick={toHome}></Button>
                        <Button onClick={toHelp}></Button>
                        <Popconfirm
                            title=""
                            description=""
                            onConfirm={addHard}
                        >
                            <Button danger loading={loadings}></Button>
                        </Popconfirm>
                        
                    </Space>
                    {
                        descriptionList.map((item, index) => {
                            return (
                                <Card
                                    style={{
                                    width: 900,
                                    }}
                                    key={'Card'+index}
                                >
                                    : <TextArea onChange={changeDescription.bind(this, index)} rows={4} placeholder={sampleDescription} maxLength={1024} />
                                    : <Input onChange={changeAction.bind(this, index)} placeholder={sampleAction} allowClear />
                                </Card>
                            )
                        })
                    }
                    <Button onClick={addNewDescription}></Button>
                </Space>
            </div>
        </div>
    )
}

export default connect(
    (state) => ({state: state.user}), 
    {
      logout: logout,
    }
  )(AnnotationUI)